.AnswersResult {
  margin: 30px auto;
  border: 2px solid rgb(62, 196, 138);
  box-shadow: 3px 3px 8px rgb(0 0 0 / 25%);
  width: 100%;
  border-radius: 15px;
  padding: 15px;
  box-sizing: border-box;
}

.AnswersResult__title {}

.AnswersResult__info {
  margin-top: 30px;
  display: flex;
  flex-direction: column;
}

.AnswersResult__btns {
  display: flex;
  justify-content: space-between;
}